import uniList from "@/components/uni-list/uni-list.vue";
import uniFab from "@/components/uni-fab/uni-fab.vue";
import uniListItem from "@/components/uni-list-item/uni-list-item.vue";
import uniCollapse from "@/components/uni-collapse/uni-collapse.vue";
import uniCollapseItem from "@/components/uni-collapse-item/uni-collapse-item.vue";
import { createNamespacedHelpers } from "vuex";
import uniPagination from "@/components/uni-pagination/uni-pagination.vue";
// import {mapState,mapActions} from "Vuex"
const { mapState, mapActions, mapMutations } = createNamespacedHelpers(
  "students"
);
export default {
  data() {
    return {
      pageSize: 5,
      current: 1,
      horizontal: "left",
      vertical: "bottom",
      direction: "horizontal",
      pattern: {
        backgroundColor: "#f8f8f8",

        selectedColor: "#d91688",
        buttonColor: "#d91688"
      },
      content: [
        // {
        //   text: "添加学生",
        // },
        // {
        //   text: "添加班级",
        // },
        {
          text: "添加学生",
          active: false
        },
        {
          text: "添加班级",
          active: false
        }
      ]
    };
  },
  components: {
    uniList,
    uniListItem,
    uniCollapse,
    uniCollapseItem,
    uniPagination,
    uniFab
  },
  mounted() {
    // console.log("21321")
    // this.dd();
    // console.log(this.getstudentsdataAsync())
    this.getstudentsdataAsync();
  },
  computed: {
    ...mapState(["studentsdata", "total"])
  },
  methods: {
    ...mapActions(["getstudentsdataAsync"]),
    ...mapMutations(["changeCurrentPage"]),
    changePage(e) {
      console.log(e.type, e.current);
      let type = e.type;
      let current = e.current;
      this.changeCurrentPage({ type, current });
      this.getstudentsdataAsync();
    },
    trigger(e) {
      console.log(e);
      this.content[e.index].active = !e.item.active;
      uni.showModal({
        title: "提示",
        content: `您${this.content[e.index].active ? "选中了" : "取消了"}${
          e.item.text
        }`,
        success: function(res) {
          if (res.confirm) {
            console.log("用户点击确定");
            // uni.navigateTo({
            //    url: '/pages/add/add'
            // });
            uni.navigateTo({
              url: "pages/add/add"
            });
          } else if (res.cancel) {
            console.log("用户点击取消");
          }
        }
      });
    },
    deletestudent(e) {
      console.log("213");
      // console.log(_id);
      console.log(e);

      uni.request({
        url: "http://127.0.0.1:3000/student/delete", //仅为示例，并非真实接口地址。
        data: {
          _id: e.target.id
        },
        // header: {
        // 	'custom-header': 'hello' //自定义请求头信息
        // },
        success: res => {
          console.log(res.data);
          this.getstudentsdataAsync();
          this.text = "request success";
          //   uni.showModal({
          //     title: "提示",
          //     content: `删除成功
          //     }`,
          //     success: function(res) {
          //       if (res.confirm) {
          //         console.log("用户点击确定");
          //       } else if (res.cancel) {
          //         console.log("用户点击取消");
          //       }
          //     }
          //   });
        }
      });
    }
  }
};
